<!-- 部门管理 -->
<template>
  <div>
    <Row :gutter="20">
      <Col :lg="4" :md="8">
        <Input v-model="departmentName" placeholder="部门名称" class="input-width"></Input>
      </Col>
      <Col :lg="4" :md="8">
        <Button type="success">
          <Icon type="md-search" />查询
        </Button>
      </Col>
    </Row>
    <br />
    <Row>
      <Button type="success" @click="mgrModal = true">
        <Icon type="md-add" />添加
      </Button>
      <Button type="error" class="margin-left">
        <Icon type="md-close" />删除
      </Button>
    </Row>
    <br />
    <Table :columns="column1" :data="data1"></Table>
    <br />
    <Page :total="100" />

    <!-- 对话框 -->
    <Modal v-model="mgrModal" title="部门管理">
      <Form ref="formItem" :model="formItem">
        <FormItem label="部门编号">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
        <FormItem label="部门名称">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
        <FormItem label="部门类型">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
        <FormItem label="负责人">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
        <FormItem label="联系电话">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="mgrModal = false">关闭</Button>
        <Button type="success">保存</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "DepartmentMgr",
  data() {
    return {
      departmentName: "",
      mgrModal: false,
      formItem: {
        no: ""
      },
      column1: [
        {
          key: "select",
          type: "selection",
          width: 60
        },
        {
          key: "no",
          title: "部门编号",
        },
        {
          title: "部门名称",
        },
        {
          title: "部门类型",
        },
        {
          title: "负责人",
        },
        {
          title: "联系电话",
        },
        {
          title: "所属机构",
        },
        {
          title: "修改",
          width: 100,
          render: (h) => {
            return h(
              "a",
              {
                on: {
                  click: () => {
                    this.mgrModal = true;
                  }
                }
              },
              [
                h("Icon", {
                  props: {
                    type: "md-create",
                    size: 24
                  }
                })
              ]
            );
          }
        }
      ],
      data1: [{ no: "aaa" }]
    };
  },
  methods: {}
};
</script>
 